import React from 'react'
import './App.css'
import {Route, Switch} from "react-router-dom";
import books from './Books';
import Notfound from './404';
// import BooksAPI from './BooksAPI' 
import SearchPage from './SearchPage';
import MainPage from './MainPage';
class BooksApp extends React.Component {
  state = {
    allBooks: books,
  }
  componentDidMount(){
    if(localStorage.getItem('books')){
      const cached = localStorage.getItem('books');
      this.setState({
        allBooks: JSON.parse(cached)
      })

    }
  }

  onDeleteBook = (book)=> {
    if(this.state.allBooks){
      this.state.allBooks.map(e => (e.id === book.id) ? (this.setState({ allBooks: this.state.allBooks.filter(e => e.id !== book.id) })) : e)
    }
  }
  componentDidUpdate(){
    localStorage.setItem('books',  JSON.stringify(this.state.allBooks));
  }
  handleChange = (f, book) => {
   let moveTo = f.target.value;
   if(this.state.allBooks){

     let moved = this.state.allBooks.slice();
     this.setState({
      allBooks:  moved.map((e)=> (
       (book.id === e.id) ? ((moveTo !== 'none') ? ({'id': e.id, 'shelf':  moveTo, 'title': e.title, 'authors': e.authors, 'imageLinks': {'thumbnail': e.imageLinks.thumbnail}}) : ({'id': e.id, 'shelf':  e.shelf, 'title': e.title, 'authors': e.authors, 'imageLinks': {'thumbnail': e.imageLinks.thumbnail}}) ) : e
      ))
     })
   }
  }
// 
  updateBooks = (list, book)=>{
    let f = {'id': book.id, 'shelf': list.target.value, 'title': book.title, 'authors': book.authors, 'imageLinks': {'thumbnail': book.imageLinks.thumbnail}}
    if(this.state.allBooks){
      this.setState({
        allBooks: this.state.allBooks.push(f)
      })
      localStorage.setItem('books', JSON.stringify(this.state.allBooks))
    }
  }
  render() {
    return (
      <div className="app">
        <Switch>
          <Route path='/search' component={()=> (
            <SearchPage pageType={'search'}  updateBooks={(book, list)=> this.updateBooks(book, list)} handleChange={(e, book, id) => this.handleChange(e, book, id)}/>
          )}/>
          <Route exact path='/' component={()=> (
          <MainPage pageType={'main'} onDeleteBook={(e, book) => this.onDeleteBook(book)} allBooks={this.state.allBooks} handleChange={(e, book, id) => this.handleChange(e, book, id)}/>
          )} />
          <Route component={Notfound}/>
        </Switch>
      </div>
    )
  }
}

export default BooksApp
