import React, {Component} from 'react';
import {Link } from 'react-router-dom';
import * as BooksAPI from './BooksAPI';
import Book from './BookComponent';
class SearchPage extends Component{
    constructor(props){
      super(props);
      this.state = {
        query: '',
        books: []
      }
    }
    updateHandler = (e, book) => {
      this.props.updateBooks(e, book);
    }
    handleChange = (e, book, id)=> {
        this.updateHandler(e, book);
        this.props.handleChange(e, book, id);
    }

    handleSearch = (e)=> {
      this.setState({query: e.target.value});
      if(e.target.value !== ''){
        this.searchTerm(this.state.query);
      }else{
        this.setState({books: []})
      }
    }
    searchTerm = (e)=> {
      BooksAPI.search(e).then(res => this.setState({books: res}) ).catch(err => console.log(err));
      setInterval(()=> {
        if(!this.state.books){
          
        }
      }, 3000)
    }
    
    render(){
        console.log(this.state)
        return (
          <div className="search-books">
            <div className="search-books-bar">
              <Link className="close-search" to='/'>Close</Link>
              <div className="search-books-input-wrapper">
                {/*
                  NOTES: The search from BooksAPI is limited to a particular set of search terms.
                  You can find these search terms here:
                  https://github.com/udacity/reactnd-project-myreads-starter/blob/master/SEARCH_TERMS.md

                  However, remember that the BooksAPI.search method DOES search by title or author. So, don't worry if
                  you don't find a specific author or title. Every search is limited by search terms.
                */}
                <input type="text" onChange={(e)=> this.handleSearch(e)} placeholder="Search by title or author"/>
                
              </div>
            </div>
            <div className="search-books-results">
              {/* {console.log(this.state.query)} */}
              <ol className="books-grid">
                {typeof(this.state.books) === 'object' ? ((this.state.books.length > 0  && this.state.books !== '' ) ? this.state.books.map((book)=> (
                  
                <li key={book.id}>
                  {book.hasOwnProperty('imageLinks') ? (
                    <Book pageType={this.props.pageType} bookDetails={book} handleChange={(e)=> this.handleChange(e, book, book.id)}/>
                    ) : ''}
                </li>
              )) : (
                // In case there is no books or there is no specific search term 
              <h1 className='lead'>No Books with this term, try to use another phrase</h1>
              )) : ''}</ol>
            </div>
          </div>
        );
    }
}

export default SearchPage;