import React, { Component } from 'react';
// import { Route, Link } from 'react-router-dom';
class Book extends Component{

  handleChange = (e) => {
    this.props.handleChange(e);
  }

  onDeleteBook = (e)=> {
    this.props.onDeleteBook(e);
  }
  render(){
    let book = this.props.bookDetails;
    return (
        <div className="book">
          
          
          <div className="book-top">
            
            <div className="book-cover" style={{ width: 128, height: 192, backgroundImage: `url(${book.imageLinks.thumbnail})` }}></div>
            <div className="book-shelf-changer">
              {this.props.pageType === 'main' ? (
                // In Case Of Main Page, Show By Shelf
              <select onChange={this.handleChange} value={book.shelf}>
                <option value="move" disabled>Move to...</option>
                <option value="currentlyReading">Currently Reading</option>
                <option value="wantToRead">Want to Read</option>
                <option value="read">Read</option>
                <option value="none">None</option>
              </select>
              ) : (
                // In Case Of Search Pag, Show as None Value of Shelf
                <select onChange={this.handleChange} value={'none'}>
                  <option value="move" disabled>Move to...</option>
                  <option value="currentlyReading">Currently Reading</option>
                  <option value="wantToRead">Want to Read</option>
                  <option value="read">Read</option>
                  <option value="none">None</option>
                </select>
              )}
            </div>
          </div>
          <div className="book-title">{book.title}</div>
          <div className="book-authors">{book.authors}</div>
          {(this.props.handleDelete) && (
            <button className="deleteBook" onClick={(e) => this.onDeleteBook(e)}>X</button>
          )}
        </div>
    );
  }
}

export default Book;