import React, {Component} from 'react';
import {Link } from 'react-router-dom';
import Book from './BookComponent'

class MainPage extends Component{
    handleChange = (e, book, id)=> {
        this.props.handleChange(e, book, id);
    }

    onDeleteBook = (e, book) => {
      this.props.onDeleteBook(e, book)
    }
    render(){
        return (
            <div className="list-books">
            <div className="list-books-title">
              <h1>MyReads</h1>
            </div>
            <div className="list-books-content">
              <div>
                <div className="bookshelf">
                  <h2 className="bookshelf-title">Currently Reading</h2>
                  <div className="bookshelf-books">
                    <ol className="books-grid" >
                      {this.props.allBooks ? (this.props.allBooks.map( book => (
                        
                        (book.shelf === 'currentlyReading') &&
                          (
                            <li key={book.id}>
                              {/* {console.log(book)} */}
                              <Book pageType={this.props.pageType} bookDetails={book} handleDelete={true} onDeleteBook={(e) => this.onDeleteBook(e, book)} handleChange={(e) => this.handleChange(e, book, book.id)}/>
                            </li>
                          )
                        )
                      )): ''}
                    </ol>
                  </div>
                </div>
                <div className="bookshelf">
                  <h2 className="bookshelf-title">Want to Read</h2>
                  <div className="bookshelf-books">
                    <ol className="books-grid">
                      {this.props.allBooks ? (this.props.allBooks.map( book => (
                        (book.shelf === 'wantToRead') &&
                          (
                          <li key={book.id}>
                            <Book pageType={this.props.pageType} bookDetails={book} handleDelete={true} onDeleteBook={(e) => this.onDeleteBook(e, book)} handleChange={(e) => this.handleChange(e, book, book.id)}/>
                          </li>)
                        )
                      )) : ''}
                    </ol>
                  </div>
                </div>
                <div className="bookshelf">
                  <h2 className="bookshelf-title">Read</h2>
                  <div className="bookshelf-books">
                      <ol className="books-grid">
                      {this.props.allBooks ? (this.props.allBooks.map(book => (
                        (book.shelf === 'read') &&
                          (
                          <li key={book.id}>
                            <Book pageType={this.props.pageType} bookDetails={book} handleDelete={true} onDeleteBook={(e) => this.onDeleteBook(e, book)} handleChange={(e) => this.handleChange(e, book, book.id)}/>
                          </li>
                          )
                        )
                        
                        )) : ''}
                    </ol>
                  </div>
                </div>
              </div>
            </div>
            <div className="open-search">
              <Link to='/search'>Add a book</Link>
            </div>
          </div>
        )
    }
}

export default MainPage;